import addToMailchimp from "gatsby-plugin-mailchimp"
import React from "react"
import styled from "styled-components"

const ResponceBlock = styled.div`
  text-align: center;

  a {
    color: #bddb00;
  }
`

const FormStyled = styled.form`
  display: flex;
`

const InputStyled = styled.input`
font-size: 1rem;
  padding: 20px; 
  // width: 400px; 
  margin-left: auto;
  border-bottom-width: 2px; 
  border-bottom-style: solid; 
  border-bottom-color: #b5dd00; 
  border-top-style: none; 
  border-right-style: none; 
  border-left-style: none; 
`

const ButtonStyled = styled.button`
  margin: auto;
  margin-left: 0;
  padding: 10px 20px;
  background-color: #b5dd00;
  box-shadow: rgba(0,0,0,0.25) 0 2px 5px;
  font-size: 0.8rem;
  border: none;
  border-radius: 25px;
  font-weight: bold;
`
export default class MailChimpForm extends React.Component {
  constructor() {
    super()
    this.state = { email: "", result: {} }
  }

  _handleSubmit = async e => {
    console.log("handle sub")
    e.preventDefault()
    const result = await addToMailchimp(this.state.email)
    console.log("result", result)
    this.setState({ result: result.result })
  }

  handleChange = event => {
    this.setState({ email: event.target.value })
  }
  render() {
    console.log(this.state.result)
    return this.state.result == "success" ? (
      <ResponceBlock>
        <h3>You have successfully subscribed.</h3>
        <p>You can download it from <span><a href="https://weareready-website.oss-us-west-1.aliyuncs.com/weareready.cn-10_free_ways.pdf?versionId=CAEQNBiBgID6nY2EyhciIGFkOGU3OGU3OGQyYjQ5MmY5YWU4NjVmYTJlZWM4NWYx" target="_blank" rel="noreferrer">HERE</a></span></p>
      </ResponceBlock>
    ) : this.state.result == "error" ? (
        <ResponceBlock>
          <h3>You have already subscribed.</h3>
           <p>You can download it from <span><a href="https://weareready-website.oss-us-west-1.aliyuncs.com/weareready.cn-10_free_ways.pdf?versionId=CAEQNBiBgID6nY2EyhciIGFkOGU3OGU3OGQyYjQ5MmY5YWU4NjVmYTJlZWM4NWYx" target="_blank" rel="noreferrer">HERE</a></span></p>
      </ResponceBlock>
    ) : (
      <FormStyled onSubmit={this._handleSubmit}>
        <InputStyled
          id="outlined-email-input"
          type="email"
          name="email"
          autoComplete="email"
          onChange={this.handleChange}
        />
        <ButtonStyled
          color="primary"
          type="submit"
        >
          Subscribe
        </ButtonStyled>
      </FormStyled>
    )
  }
}